<template>
    <div class="container mt-3">
        
        <div class="container">
            <div v-if="ventas.length > 0">
                <h1>Total de ventas: {{ventas.length}}</h1>
            </div>
            <table class="table table-striped">
                <thead>
                    <tr>
                        <th>N째</th>
                        <th>C처digo</th>
                        <th>Nombre</th>
                        <th>Precio</th>
                    </tr>
                </thead>
                <tbody>
                    <tr v-for="(venta, i) in ventas" :key='i'>
