                        <td>{{venta.codigo}}</td>
                        <td>{{venta.nombre}}</td>
                        <td>${{venta.precio}}</td>
                    </tr>
                    <tr>
                        <td></td>
                        <td></td>
                        <th>Total ventas:</th>
                        <th class="text-success">${{total}}</th>
                    </tr>
                </tbody>
            </table>   
        </div>

    </div>
</template>

<script>
import { mapState } from 'vuex'
export default {
    computed:{
